<template>
  <table v-if="isCalenderOpen">
    <thead>
      <tr>
        <th></th>
        <th v-for="day in days" :key="day">{{ day }}</th>
      </tr>
    </thead>
    <tbody>
      <slot></slot>
    </tbody>
  </table>
</template>

<script>
import { teamService } from '../services/teamService';
export default {
  props: { hours: Array, isCalenderOpen: Boolean },
  data() {
    return {
      days: teamService.getWeekData().days
    }
  }
};
</script>
