<template>
  <form
    class="edit-calender-form flex flex-wrap"
    @submit.prevent="onSubmit"
  >
    <select
      name="member"
      @change="$emit('changeMember', memberId)"
      v-model="memberId"
    >
      <option v-for="member in members" :key="member.id" :value="member.id">
        {{ member.name }}
      </option>
    </select>
    <select name="day" v-model="day">
      <option v-for="day in days" :key="day">{{ day }}</option>
    </select>
    <div class="hour-input flex">
      <select name="from-hour" v-model="fromHour">
        <option v-for="hour in hours" :key="hour">{{ hour }}</option>
      </select>
      <h1>-</h1>
      <select name="to-hour" v-model="toHour">
        <option v-for="hour in hours" :key="hour">{{ hour }}</option>
      </select>
    </div>
    <select name="task" v-model="task">
      <option v-for="task in tasks" :key="task">{{ task }}</option>
    </select>
    <button>save</button>
  </form>
</template>

<script>
export default {
  // props: ['members', 'days', 'hours', 'tasks'],
  props: {
    members: Array,
    days: Array,
    hours: Array,
    tasks: Array,
  },
  data() {
    return {
      memberId: '',
      day: 'Sunday',
      fromHour: '08:00',
      toHour: '08:00',
      task: 'Queue',
    };
  },
  created() {
    this.memberId = this.members[0]?.id;
  },
  methods: {
    onSubmit() {
      this.$emit('updateCalender', this.$data);
    },
  },
};
</script>

