<template>
  <article class="member-calender-preview">
    <h1 @click="toggleCalenderOpen">Main Calender</h1>
    <week-table :isCalenderOpen="isCalenderOpen">
      <tr :key="hour" v-for="hour in hours">
        <th>{{ hour }}</th>
        <td v-for="day in days" :key="day">
          <p
            v-for="(value, task) in taskNumMap[day][hour]"
            :class="{ opening: value.members.length === 5 && task === 'Queue' }"
            :key="task"
            :title="value.members"
          >
            {{ task + ': ' + value.members.length }}
          </p>
        </td>
      </tr>
    </week-table>
  </article>
</template>

<script>
import WeekTable from './WeekTable.vue';
export default {
  components: { WeekTable },
  props: ['taskNumMap', 'days', 'hours'],
  data() {
    return {
      isCalenderOpen: false,
    };
  },
  methods: {
    toggleCalenderOpen() {
      this.isCalenderOpen = !this.isCalenderOpen;
    },
  },
  computed: {},
};

