<template>
  <section class="dashboard">
    <article class="free-time-chart">
      <h1>Members free time (hours per week)</h1>
      <pie-chart
        v-if="memberFreeHoursMap['Hadas']"
        class="bar-chart"
        :memberFreeHoursMap="memberFreeHoursMap"
      />
    </article>
    <article class="queue-chart">
      <h1>Members time in queue</h1>
      <bar-chart
        v-if="memberFreeHoursMap['Hadas']"
        class="bar-chart"
        :memberFreeHoursMap="memberQueueHoursMap"
      />
    </article>
  </section>
</template>

<script>
import PieChart from '../cmps/chart/PieChart.vue';
import BarChart from '../cmps/chart/BarChart.vue';

export default {
  async created() {
    await this.loadMembers();
    console.log('members', this.membersToShow);
    console.log('memberFreeHoursMap', this.memberFreeHoursMap);
  },
  methods: {
    async loadMembers() {
      await this.$store.dispatch({ type: 'loadMembers' });
    },
  },
  computed: {
    memberFreeHoursMap() {
      return this.$store.getters.memberFreeHoursMap('Free');
    },
    memberQueueHoursMap() {
      return this.$store.getters.memberFreeHoursMap('Queue');
    },
  },
  components: {
    PieChart,
    BarChart,
  },
};
</script>
